@font-face {
  font-family: "Flaticon";
  src: url("./Flaticon.eot");
  src: url("./Flaticon.eot?#iefix") format("embedded-opentype"),
       url("./Flaticon.woff") format("woff"),
       url("./Flaticon.ttf") format("truetype"),
       url("./Flaticon.svg#Flaticon") format("svg");
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: "Flaticon";
    src: url("./Flaticon.svg#Flaticon") format("svg");
  }
}

[class^="flaticon-"]:before, [class*=" flaticon-"]:before,
[class^="flaticon-"]:after, [class*=" flaticon-"]:after {   
  font-family: Flaticon;
font-style: normal;
}

.flaticon-prescription:before { content: "\f100"; }
.flaticon-add-to-shopping-cart:before { content: "\f101"; }
.flaticon-medical-history:before { content: "\f102"; }
.flaticon-line:before { content: "\f103"; }
.flaticon-open-book:before { content: "\f104"; }
.flaticon-checked:before { content: "\f105"; }
.flaticon-tablet-and-pill:before { content: "\f106"; }
.flaticon-calendar:before { content: "\f107"; }
.flaticon-stethoscope:before { content: "\f108"; }


